h1,
h2,
h3,
h4,
h5,
p {
    margin: 0;
    padding: 0;
}

ul,
li {
    list-style: none;
    padding: 0;
    margin: 0;
}

a {
    text-decoration: none;
    color: inherit;
}


img {
    display: block;
    max-width: 100%;
    height: auto;
}

picture {
    padding: 0;
    margin: 0;
    width: 100%;
    text-decoration: none;
    list-style: none;
    text-decoration: none
}